import React, { useRef, useEffect, useState } from 'react';
import { motion, useScroll, useSpring, useTransform, useAnimation, LazyMotion, domAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import FloatingCard from './FloatingCard';
import { generateInitialPositions } from './animationUtils';
import './Skills.css';

interface Skill {
  title: string;
  description: string;
  details: string[];
  icon: string;
}

interface FloatingCardFieldProps {
  skills: Skill[];
  onTransitionComplete: () => void;
}

// Pre-compute initial positions on the server
const precomputedPositions = (totalCards: number) => {
  return Array.from({ length: totalCards }, (_, index) => {
    const radius = typeof window !== 'undefined' 
      ? Math.min(window.innerWidth, window.innerHeight) * 0.3 
      : 500;
    const angle = (index / totalCards) * Math.PI * 2;
    return {
      x: Math.cos(angle) * radius,
      y: Math.sin(angle) * radius,
      z: -1500 - (index * 100),
      rotateX: Math.sin(angle) * 30,
      rotateY: Math.cos(angle) * 30,
    };
  });
};

const FloatingCardField: React.FC<FloatingCardFieldProps> = ({ skills, onTransitionComplete }) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const controls = useAnimation();
  const [isFloating, setIsFloating] = useState(true);
  const positions = precomputedPositions(skills.length);

  // Intersection observer setup
  const { ref: inViewRef, inView } = useInView({
    threshold: 0.1,
  });

  // Scroll setup with optimized config
  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start end", "end start"],
    layoutEffect: false
  });

  // Smooth scroll progress with optimized settings
  const smoothProgress = useSpring(scrollYProgress, {
    damping: 20,
    stiffness: 90,
    mass: 0.5,
    restSpeed: 0.5,
    restDelta: 0.001
  });

  // Start floating animation when in view
  useEffect(() => {
    if (inView && isFloating) {
      controls.start("floating");
    } else {
      controls.start("static");
    }
  }, [inView, isFloating, controls]);

  // Handle scroll progress with debouncing
  useEffect(() => {
    let timeoutId: NodeJS.Timeout;
    
    const unsubscribe = smoothProgress.on("change", (value) => {
      clearTimeout(timeoutId);
      
      timeoutId = setTimeout(() => {
        setIsFloating(value < 0.1);
        if (value >= 0.8 && inView) {
          onTransitionComplete();
        }
      }, 16); // ~60fps
    });

    return () => {
      clearTimeout(timeoutId);
      unsubscribe();
    };
  }, [smoothProgress, inView, onTransitionComplete]);

  return (
    <LazyMotion features={domAnimation}>
      <motion.div 
        ref={(el) => {
          // @ts-ignore - combining refs
          containerRef.current = el;
          inViewRef(el);
        }}
        className="floating-card-field-wrapper"
        style={{ 
          position: 'relative',
          minHeight: '300vh',
        }}
        initial={false}
      >
        <div className="floating-card-field">
          <div className="floating-cards-container">
            {skills.map((skill, index) => (
              <FloatingCard
                key={skill.title}
                skill={skill}
                index={index}
                scrollProgress={smoothProgress}
                totalCards={skills.length}
                isFloating={isFloating}
                animate={controls}
                initialPosition={positions[index]}
              />
            ))}
          </div>
        </div>
      </motion.div>
    </LazyMotion>
  );
};

export default FloatingCardField;
