import React, { useMemo } from 'react';
import { motion, MotionValue, useTransform, AnimationControls, Variants } from 'framer-motion';
import { generateTransitionConfig } from './animationUtils';

interface Skill {
  title: string;
  description: string;
  details: string[];
  icon: string;
}

interface Position {
  x: number;
  y: number;
  z: number;
  rotateX: number;
  rotateY: number;
}

interface FloatingCardProps {
  skill: Skill;
  index: number;
  scrollProgress: MotionValue<number>;
  totalCards: number;
  isFloating: boolean;
  animate: AnimationControls;
  initialPosition: Position;
}

const FloatingCard: React.FC<FloatingCardProps> = ({
  skill,
  index,
  scrollProgress,
  totalCards,
  isFloating,
  animate,
  initialPosition
}) => {
  // Calculate grid position
  const columns = Math.ceil(Math.sqrt(totalCards));
  const row = Math.floor(index / columns);
  const col = index % columns;
  const gridX = (col - (columns - 1) / 2) * 300;
  const gridY = (row - Math.floor((totalCards - 1) / columns) / 2) * 300;

  // Use server-side computed initial position
  const { x: randomX, y: randomY, z: randomZ, rotateX: randomRotateX, rotateY: randomRotateY } = initialPosition;

  // Get transition config
  const transitionConfig = useMemo(() => generateTransitionConfig(index), [index]);

  // Animation variants with server-side computed values
  const variants: Variants = {
    floating: {
      x: randomX,
      y: randomY,
      z: randomZ,
      rotateX: randomRotateX,
      rotateY: randomRotateY,
      transition: {
        ...transitionConfig,
        repeat: Infinity,
        repeatType: "reverse" as const,
        type: "tween",
      }
    },
    static: {
      x: 0,
      y: 0,
      z: 0,
      rotateX: 0,
      rotateY: 0,
      transition: {
        type: "spring",
        damping: 20,
        stiffness: 90
      }
    }
  };

  // Transform values for scroll transition
  const x = useTransform(scrollProgress, [0, 0.5], [randomX, gridX], {
    clamp: false
  });
  const y = useTransform(scrollProgress, [0, 0.5], [randomY, gridY], {
    clamp: false
  });
  const z = useTransform(scrollProgress, [0, 0.5], [randomZ, 0], {
    clamp: false
  });
  const rotateX = useTransform(scrollProgress, [0, 0.5], [randomRotateX, 0], {
    clamp: false
  });
  const rotateY = useTransform(scrollProgress, [0, 0.5], [randomRotateY, 0], {
    clamp: false
  });
  const scale = useTransform(scrollProgress, [0, 0.5], [0.8, 1], {
    clamp: false
  });

  return (
    <motion.div
      className="floating-card"
      style={{
        position: 'absolute',
        x,
        y,
        z,
        rotateX,
        rotateY,
        scale,
        transformStyle: 'preserve-3d',
        willChange: 'transform'
      }}
      variants={variants}
      animate={isFloating ? "floating" : "static"}
      whileHover={!isFloating ? { scale: 1.05, z: 50 } : undefined}
      transition={{
        type: "spring",
        damping: 20,
        stiffness: 90
      }}
    >
      <motion.div 
        className="card-content"
        style={{
          willChange: 'transform'
        }}
      >
        <div className="card-icon">{skill.icon}</div>
        <h3>{skill.title}</h3>
        <p>{skill.description}</p>
        <div className="skill-details">
          {skill.details.map((detail, i) => (
            <span key={i} className="skill-tag">
              {detail}
            </span>
          ))}
        </div>
      </motion.div>
    </motion.div>
  );
};

export default FloatingCard;
